import authenticatorLogoUrl from './authenticatorLogoUrl';

const normalizeEpm = item => {
  const appId = item.applicationId;
  const accId = item.companyUserAccountId;
  return {
    ...item,
    detailPath: `/epm-details/${appId}/${accId}`,
    displayName: item.applicationName,
    id: `${appId}-${accId}`,
    logo: authenticatorLogoUrl(item.epmAppSettings.publicService.slug, 32),
  };
};

export default normalizeEpm;
