import normalizeApplication from './normalizeApplication';
import normalizeAuthenticator from './normalizeAuthenticator';
import normalizeEpm from './normalizeEpm';
import normalizeSharedAccount from './normalizeSharedAccount';

const normalizeItem = item => {
  if (item.epmAppSettings) {
    return normalizeEpm(item);
  } else if (item.applicationId) {
    return normalizeApplication(item);
  } else if (item.isShared) {
    return normalizeSharedAccount(item);
  } else {
    return normalizeAuthenticator(item);
  }
};

export default normalizeItem;
