import React, { Component } from 'react';
import hostAccountSimilarity from '../common/hostAccountSimilarity';
import normalizeItem from '../common/normalizeItem';
import onLogoError from '../common/onLogoError';
import sortAuthenticators from '../common/sortAuthenticators';
import sortBy from '../common/sortBy';
import { observe } from '../common/store';
import orca18 from '../images/orca-18.png';
import './App.css';

const hostSimilarity = hostAccountSimilarity(
  new URL(location.href).searchParams.get('host'),
);

const handleCloseClick = event => {
  event.preventDefault();
  window.parent.postMessage({ action: 'close' }, '*');
};

const login = ({ id, isShared }) =>
  window.parent.postMessage({ action: 'login', id, isShared }, '*');

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: null,
      showAll: false,
    };
  }

  componentDidMount() {
    this.subscription = observe('authenticators', 'passwordmanagers').subscribe(
      ({ authenticators, passwordmanagers }) =>
        this.setState({
          items:
            authenticators &&
            passwordmanagers &&
            sortAuthenticators([...authenticators, ...passwordmanagers])
              .filter(i => i.ssoEnabled)
              .map(normalizeItem),
        }),
    );
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  getFilteredItems() {
    if (!this.state.items) return [];
    const items = sortBy(hostSimilarity)(this.state.items).reverse();
    return this.state.showAll ? items : items.filter(hostSimilarity);
  }

  render() {
    const filteredItems = this.getFilteredItems();
    return (
      <div>
        <div className="header">
          <img src={orca18} />
          <div className="link">
            <a href="" className="active">
              {this.state.showAll ? 'Login with another account' : 'Login as'}
            </a>
          </div>
          <a href="" className="close" onClick={handleCloseClick} />
        </div>
        {this.state.items ? (
          <div>
            {filteredItems.length ? (
              <table className="table">
                <tbody className="tbody">
                  {filteredItems.map(i => (
                    <tr key={i.id} className="table__row">
                      <td className="table__cell">
                        <img
                          src={i.logo}
                          onError={onLogoError}
                          className="table__img"
                        />
                      </td>
                      <td className="table__cell">
                        <p className="table__p">{i.displayName}</p>
                        <strong className="table__strong">{i.username}</strong>
                      </td>
                      <td className="table__cell">
                        <button
                          onClick={() => login(i)}
                          className="table__button"
                        >
                          LOGIN
                        </button>
                      </td>
                    </tr>
                  ))}
                  {this.state.showAll ? null : (
                    <tr className="table__row">
                      <td className="table__cell">
                        <button
                          onClick={() => this.setState({ showAll: true })}
                          className="table__button"
                        >
                          More
                        </button>
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            ) : (
              <div className="info">
                <p className="info__p">
                  You have not added this website yet to SAASPASS.
                </p>
                <p className="info__p">You can add this after you login.</p>
                <p className="info__p">
                  Or you can use some of your{' '}
                  <button onClick={() => this.setState({ showAll: true })}>
                    Other
                  </button>{' '}
                  accounts
                </p>
              </div>
            )}
          </div>
        ) : (
          <div className="info">
            <p className="info__p">You are not logged in.</p>
            <p className="info__p">Please log in and try again.</p>
          </div>
        )}
      </div>
    );
  }
}

export default App;
