import authenticatorLogoUrl from './authenticatorLogoUrl';

const normalizeAuthenticator = item => {
  return {
    ...item,
    detailPath: `/account-details/${item.id}`,
    displayName: item.displayname || item.publicService.serviceName,
    logo: authenticatorLogoUrl(item.publicService.slug, 32),
  };
};

export default normalizeAuthenticator;
